.Core_Container {
  position: absolute;
  left: 50%;
  top: 0;
  transform: translate(-50%, 40%);
  z-index: 5;
}
.fullscreen {
  height: 91% !important;
  width: 96% !important;
  transform: translate(-50%, 5%);
}
.hide {
  height: 30vh;
  width: 30vh;
  opacity: 0;
  pointer-events: none;
}
.unhide {
  height: 60vh;
  width: 60vh;
  position: absolute;
  background: rgb(99, 99, 99);
  pointer-events: fill;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  visibility: visible;
  pointer-events: fill;
  opacity: 1;
}

.Core_Header {
  display: flex;
  justify-content: space-evenly;
  margin-top: 3vh;
}
.core_title {
  border: none;
  outline: none;
  border-bottom: 0.2vh solid rgb(207, 207, 207);
  background: none;
  color: rgb(207, 207, 207);
  font-size: 2vh;
}
::placeholder {
  color: rgb(207, 207, 207);
}
.titleActive {
  margin-top: 0.2vh;
  border-bottom: 0.2vh solid cyan;
}
.titleInactive {
  border-bottom: 0.2vh solid rgb(207, 207, 207);
}
.coreButton {
  border: none;
  width: 5vh;
  height: 5vh;
  background-size: cover;
  outline: none;
  transition-duration: 0.2s;
  transition-property: background-color, border-radius;
}
.Fullscreen {
  background-image: url("./images/fullscreen.svg");
  background-color: rgb(99, 99, 99);
}

.Fullscreen_Mode_title {
  width: 100vh;
  height: 7vh;
  font-size: 3vh;
  text-align: center;
}
.Fullscreen_Mode_textile {
  height: 65vh;
  width: 92.5%;
  font-size: 70%;
  margin-top: 44.5vh;
}

.Fullscreen_Mode_exit {
  position: absolute;
  visibility: hidden;
}
.Fullscreen_Mode_fullscreen {
  position: absolute;
  right: 5%;
}
.Exit {
  background-image: url("./images/exit.svg");
  background-color: rgb(99, 99, 99);
}
.coreButton:hover {
  border-radius: 50%;
  background-color: cyan;
}
textarea {
  margin-left: 50%;
  margin-top: 40%;
  padding: 5vh;
  padding-top: 2vh;
  width: 50vh;
  height: 30vh;
  font-size: 1.85vh;
  outline: none;
  color: rgb(207, 207, 207);
  resize: none;
  transform: translate(-50%, -50%);
  background: none;
  border: none;
  border-top: 0.2vh solid rgb(207, 207, 207);
  transform-origin: top;
}
