.passage {
  position: absolute;
  z-index: 1;
}
.passage_index {
  position: absolute;
  height: 100px;
  width: 100px;
  padding: 12px;
  transform: translate(-50%, -50%);
  background: rgba(66, 166, 266, 0.7);
  border: none;
  cursor: default;
}
.top {
  z-index: 2 !important;
}
.redify {
  background: rgb(255, 67, 67, 0.7);
}
.greenify {
  background: rgb(0, 255, 0, 0.7);
}
.bluify {
  background: rgba(66, 166, 266, 0.7);
}
.redify2 {
  background: red !important;
}
.greenify2 {
  background: greenyellow !important;
}
.bluify2 {
  background: cyan !important;
}
.options_container {
  position: absolute;
  left: 0;
  transform: translate(-50px, 70px);
}
.options {
  position: absolute;
  opacity: 0;
  z-index: 5;
  width: 100px;
  height: 30px;
  display: flex;
  pointer-events: none;
  background: white;
  justify-content: space-between;
  align-items: center;
}
.options > button {
  object-fit: contain;
  border: none;
  width: 30px;
  height: 100%;
  background-size: cover;
  outline: none;
  cursor: pointer;
}
.delete_btn {
  background-image: url("./images/delete.svg");
  background-color: white;
}
.palette_btn {
  background-image: url("./images/palette.svg");
  background-color: white;
}
.palette {
  position: absolute;
  width: 100px;
  height: 100px;
  z-index: -5 !important;
  background: #caa472;
  transform: translate(70%, 0px);
  opacity: 0;
  pointer-events: none;
  border-bottom-right-radius: 100%;
  display: flex;
  justify-content: center;
}
.palette_choice {
  width: 20px;
  height: 20px;
  border-radius: 50%;
}
.red_pale {
  background: rgb(255, 67, 67);
  transform: translate(-10px, 67px);
}
.green_pale {
  background: rgb(0, 255, 0);
  transform: translate(4px, 47px);
}
.blue_pale {
  background: rgba(66, 166, 266);
  transform: translate(7px, 10px);
}
.line {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 58px;
  height: 52px;
  opacity: 0;
}
.line_inner {
  position: absolute;
  display: flex;
  justify-content: space-around;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform-origin: top;
  transform: translate(7px, -3px) rotateZ(48deg) scale(0.5, 1);
}
.line_inner > div {
  width: 2px;
  height: 20px;
  background: cyan;
}
.line_inner > div:nth-child(1) {
  height: 5px;
}
.line_inner > div:nth-child(2) {
  height: 25px;
}
.line_inner > div:nth-child(3) {
  height: 45px;
}
.visible {
  pointer-events: fill;
  opacity: 1;
}
.empty_mode {
  clip-path: polygon(0 0, 0 100%, 35% 100%, 50% 100%, 100% 55%, 100% 0);
}
.passage_text {
  margin: 0;
  cursor: default;
  word-wrap: break-word;
}
.title {
  font-size: 16px;
}
.textile {
  font-weight: 500;
  font-size: 12px;
  width: 107px;
  height: 65px;
  overflow: hidden;
  word-wrap: break-word;
}
